module.exports = "<!--\r\n1. Prop的大小写\r\n2. Prop的类型\r\n-->\r\n\r\n\r\n<template>\r\n    <div>\r\n        <div class=\"case-item\">\r\n            <h4>1. Prop的大小写</h4>\r\n            <blog-post :post-title=\"postTitle\"></blog-post>\r\n        </div>\r\n\r\n        <div class=\"case-item\">\r\n            <h4>2. Prop的类型</h4>\r\n            <child-type\r\n                    :title=\"title\"\r\n                    :likes=\"likes\"\r\n                    :isPublished=\"isPublished\"\r\n                    :commentIds=\"commentIds\"\r\n                    :author=\"author\">\r\n            </child-type>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\n    import {Vue, Component, Watch} from 'vue-property-decorator'\r\n    import $Vue from 'vue/dist/vue.js'\r\n\r\n    $Vue.component('blog-post', {\r\n        props: ['postTitle'],\r\n        template: '<div> {{ postTitle }} </div>'\r\n    })\r\n\r\n    $Vue.component('child-type', {\r\n        props: {\r\n            title: String,\r\n            likes: Number,\r\n            isPublished: Boolean,\r\n            commentIds: Array,\r\n            author: Object\r\n        },\r\n        template: '<div>' +\r\n        '<div> String: {{ title }} </div>' +\r\n        '<div> likes: {{ likes }} </div>' +\r\n        '<div> Boolean: {{ isPublished.toString() }} </div>' +\r\n        '<div> Array: {{ JSON.stringify(commentIds) }} </div>' +\r\n        '<div> Object: {{ JSON.stringify(author) }} </div>' +\r\n        '</div>'\r\n    })\r\n\r\n    @Component({\r\n        components: {\r\n            'blog-post': $Vue.component('blog-post'),\r\n            'child-type': $Vue.component('child-type')\r\n        }\r\n    })\r\n    export default class PropCasing extends Vue {\r\n        postTitle = 1;\r\n        title = 'hi'\r\n        likes = 2\r\n        isPublished = false\r\n        commentIds = [1, 2]\r\n        author = {name: 'goku'}\r\n    }\r\n</script>\r\n\r\n\r\n\r\n";