module.exports = "<!--\r\ntype 可以是下列原生构造函数中的一个：\r\n\r\nString\r\nNumber\r\nBoolean\r\nArray\r\nObject\r\nDate\r\nFunction\r\nSymbol\r\n-->\r\n\r\n\r\n<template>\r\n    <div>\r\n        <div class=\"case-item\">\r\n            <h4>类型检测</h4>\r\n            <my-component\r\n                    :propA=\"propA\"\r\n                    :propB=\"propB\"\r\n                    :propC=\"propC\"\r\n                    :propF=\"propF\">\r\n            </my-component>\r\n        </div>\r\n\r\n    </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\n    import {Vue, Component, Watch} from 'vue-property-decorator'\r\n    import $Vue from 'vue/dist/vue.js'\r\n\r\n    $Vue.component('my-component', {\r\n        props: {\r\n            // 基础的类型检查 (`null` 匹配任何类型)\r\n            propA: Number,\r\n            // 多个可能的类型\r\n            propB: [String, Number],\r\n            // 必填的字符串\r\n            propC: {\r\n                type: String,\r\n                required: true\r\n            },\r\n            // 带有默认值的数字\r\n            propD: {\r\n                type: Number,\r\n                default: 100\r\n            },\r\n            // 带有默认值的对象\r\n            propE: {\r\n                type: Object,\r\n                // 对象或数组且一定会从一个工厂函数返回默认值\r\n                default () {\r\n                    return {message: 'hello'}\r\n                }\r\n            },\r\n            // 自定义验证函数\r\n            propF: {\r\n                validator (value) {\r\n                    // 这个值必须匹配下列字符串中的一个\r\n                    return ['success', 'warning', 'danger'].indexOf(value) !== -1\r\n                }\r\n            }\r\n        },\r\n        template: '<div>{{ JSON.stringify($props)}}</div>'\r\n    })\r\n    @Component({\r\n        components: {\r\n            'my-component': $Vue.component('my-component')\r\n        }\r\n    })\r\n    export default class PropValidation extends Vue {\r\n        propA = 1\r\n        // 多个可能的类型\r\n        propB = 'test'\r\n        // 必填的字符串\r\n        propC = 'testc'\r\n        // 带有默认值的数字\r\n\r\n        // 带有默认值的对象\r\n\r\n        // 自定义验证函数\r\n        propF = 'success'\r\n    }\r\n</script>\r\n\r\n\r\n\r\n";